body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*{
  list-style: none;
}
@charset "UTF-8";
* {
  margin: 0;
  padding: 0; }

.searchbar {
  margin: 0 1em;
  margin-top: 10px; }
  .searchbar input {
    width: 100%;
    height: 2em; }

.key-list {
  overflow: hidden;
  margin: 10px 1em; }

.key {
  color: white;
  float: left;
  border-radius: 5px;
  background: #207df8;
  padding: 0 4px;
  margin-right: 2px;
  margin-bottom: 2px;
  cursor: pointer; }

.video-list {
  overflow: auto;
  margin: 1em; }

.title, .video .title {
  white-space: normal;
  line-height: 1.2;
  height: 2.4em;
  text-align: left;
  word-wrap: break-word;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  overflow: hidden; }

.video {
  margin-right: 4px;
  display: inline-block;
  width: 20em;
  margin-bottom: 24px;
  overflow: hidden;
  cursor: pointer; }

.video-detail-container {
  margin-bottom: 1em; }
  .video-detail-container .video-detail {
    display: inline-block;
    margin-left: 1em;
    position: relative; }
    @media (max-width: 750px) {
      .video-detail-container .video-detail {
        width: 100%;
        padding-bottom: 56.25%;
        /* 16:9 비율인 경우 */ } }
    @media (min-width: 750px) {
      .video-detail-container .video-detail {
        width: 60%;
        padding-bottom: 33.75%; } }
    .video-detail-container .video-detail iframe {
      position: absolute;
      top: 0;
      left: 0;
      width: 94%;
      height: 98%; }
  .video-detail-container .title {
    padding: 0 1em; }
    @media (max-width: 750px) {
      .video-detail-container .title {
        width: 95%; } }
    @media (min-width: 750px) {
      .video-detail-container .title {
        width: 57%; } }

